@import 'normalize';
@import 'footer';
/*
====================================
Default Styling
====================================
*/

img {
	max-width: 100%;
}

body {
	color: #333;
}

.container {
	max-width: 1080px;
	margin: 0 auto;
	width: 98%;

}
.cContainer{
 	display: flex
}

.content {
	width: 90%;
	margin: 0 auto;
}

.full {
	width: 100%;
}
///
// fonts 

$headfont: 'Montserrat', sans-serif;
$bodyfont:'Overpass', sans-serif;

@mixin fontSize($sizeValue){
    font-size: $sizeValue * 1px;
    font-size: ($sizeValue/10) * 1rem;
}
.flex {
	display: flex;
	flex-flow: row wrap;
}
// P's & H's

h1{
	@include fontSize(35);
	font-family: $headfont;
	color:black;

}
h2 {
	@include fontSize(14);
	font-family: $headfont;
	color:black;
	
	}
h3 {
	@include fontSize(15);
	font-family: $headfont;
	color:black;
	margin: 0;
	letter-spacing: 1px;
	text-transform: uppercase;

}
.headerFont {
	    margin-top: 25px;
}
h4{
	@include fontSize(10);
	font-family: $headfont;
	color:black;
}

a {
	text-decoration: none;
	color:black;
	&:hover {
		color: #grey;
    	}
}
p {	
	letter-spacing: 1px;
	font-family: $bodyfont;
	color:black;
	@include fontSize(10);
	a {
		color:#87000F;
		text-decoration: none;
		&:hover {
			color: #909090;
		}
	}

}

img {
	max-width: 100%;
}

body {
	background: #fff url('images/bg.gif') top left no-repeat;
	background-attachment:fixed;
	color: #333;
	font-family: $bodyfont;
}

//header 
ul.menu {
	display: flex;
    list-style: none;
    padding-left: 8%;
    position: absolute;
    top: 90px;

	li {
		display: inline;
		margin-right: 10px;
		a {
				text-decoration: none;
				&:hover {
					text-decoration: line-through;
			    	}
		}
	}
}
#menu-item-36{
	    margin-left: 28%;
}
#menuToggle {
	margin-top: 20px;
	cursor: pointer;
 }
.away {
	display: none;
}
.creditsSide{
	width: 10%;
}
.creditsToggle{
	position: relative;
    top: 125px;
    left: -24px;
}
.addition{
	position: relative;
	left: 650%;
	top: 125px;
}
.creditsContent {
	position: absolute;
    top: 160px;
    margin: 6px;
    border: 3px solid black;
    border-radius: 10px;
    padding: 13px;
    background: white;
    width: 65%;
    z-index: 10;
    max-width: 723px;
}
.strikeMenu {
	text-decoration: line-through;
}

//front page

.itemLinkeach {
	a {
		img{
			max-width: 130px;	
		}
	}
	
	
}

//projects

.sidebarProjects, .tagBody {
	display: flex;
}








